@mixin before($display: block) {
  &:before {
    content: "";
    display: $display;
    @content; } }

@mixin after($display: block) {
  &:after {
    content: "";
    display: $display;
    @content; } }

@mixin make-bg($image, $size: cover, $position: 50% 50%) {
  background-image: url($image);
  background-size: $size;
  background-repeat: no-repeat;
  background-position: $position; }

@mixin absolute {
  @extend .fullsize-absolute; }

@mixin hover {
  @media (hover), (min-width:0\0), (min--moz-device-pixel-ratio:0) {
    @media (min-width: $media_md) {
      &:hover {
        @content; } } } }

@mixin tap {
  &:active, &:target {
    @content; } }

@mixin media($width) {
  @media(max-width: $width) {
    @content; } }

@mixin make-col($size) {
  flex: 0 0 percentage($size / $grid-columns);
  max-width: percentage($size / $grid-columns); }

@mixin make-containerSide($side, $plus: 0) {
  @for $i from length($containerWidths) through 1 {
    @media(min-width: nth($mediaList, $i)) {
      #{$side}: calc((100vw - #{nth($containerWidths, $i)}) / 2 + #{$containerPadding} + #{$plus + px}); } } }

@function em($pixels, $context: 16) {
  @return #{$pixels/$context}em; }
